import defined from '../../../Core/defined.js';
import Cartesian3 from '../../../Core/Cartesian3.js';
import Matrix4 from '../../../Core/Matrix4.js';

export default class CreatePhysicalData {
}

var negateRelativeToCenter = new Cartesian3();
var negateRelativeToCenterMatrix = new Matrix4();
var relativePosition = new Cartesian3();

CreatePhysicalData.createPhysicalArray = function (physical, lbSpaMgr, relativeToCenter, positions, indices) {
    var hasRelativeToCenter = defined(relativeToCenter);
    relativeToCenter = hasRelativeToCenter ? relativeToCenter : Cartesian3.ZERO;
    Cartesian3.negate(relativeToCenter, negateRelativeToCenter);
    Matrix4.fromTranslation(negateRelativeToCenter, negateRelativeToCenterMatrix);

    var physicalVertices = new Float32Array(positions.length * 3.0);
    var indexVertices = 0;
    for (var index = 0; index < positions.length; ++index) {
        var position = positions[index];
        Matrix4.multiplyByPoint(negateRelativeToCenterMatrix, position, relativePosition);

        physicalVertices[indexVertices++] = relativePosition.x;
        physicalVertices[indexVertices++] = relativePosition.y;
        physicalVertices[indexVertices++] = relativePosition.z;
    }

    var pPrimitive = new physical.LBSpaPrimitive();
    initSpaTrianglePrimitive(pPrimitive, physicalVertices, indices);
    var pPrimitiveSpatial = lbSpaMgr.CreateTriangleSpatial(pPrimitive);

    //serial write
    var pSerialWrite = new physical.LBSpaSerial();
    pSerialWrite.WriteSpatial(pPrimitiveSpatial);
    physical.destroy(pPrimitiveSpatial);
    var physicalArray = new Uint8Array(pSerialWrite.GetBufferSize());
    for (var i = 0; i < physicalArray.length; ++i) {
        physicalArray[i] = pSerialWrite.GetBufferVal(i);
    }
    physical.destroy(pSerialWrite);

    return physicalArray;
};

function initSpaTrianglePrimitive(pPrimitive, pPtAry, pIndexAry) {
    pPrimitive.SetPtValNum(pPtAry.length, false);//true desc have batch
    var i;
    for (i = 0; i < pPtAry.length; ++i) {
        pPrimitive.SetPtValVal(i, pPtAry[i]);
    }
    pPrimitive.SetIndexNum(pIndexAry.length, false);//need edge
    for (i = 0; i < pIndexAry.length; ++i) {
        pPrimitive.SetIndexVal(i, pIndexAry[i]);
    }
}
